export default {
	data() {
		return {
			source: 0,
			addressList: []
		}
	},
	onLoad(option){
		console.log(option.source);
		this.source = option.source;
		this.loadData()
	},
	methods: {
		async loadData() {
			let addressList = uni.getStorageSync('address_list') || await this.$api.json('addressList');
			
			this.addressList = addressList;	
		},
		//选择地址
		checkAddress(item){
			if(this.source == 1){
				//this.$api.prePage()获取上一页实例，在App.vue定义
				this.$api.prePage().addressData = item;
				uni.navigateBack()
			}
		},
		addAddress(type, item){
			uni.navigateTo({
				url: `/pages/address/addressManage?type=${type}&data=${JSON.stringify(item)}`
			})
		},
		//添加或修改成功之后回调
		refreshList(data, type){
			//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
			if(data && data.id) {
				let index = this.addressList.findIndex((x) => {
					return x.id === data.id;
				})
				this.addressList.splice(index,1,data)
			} else {
				data.id = this.addressList.length + 1;
				this.addressList.unshift(data);
			}
			
			uni.setStorageSync('address_list',this.addressList);
			console.log(data, type);
		}
	}
}
